@keyframes check-in {
    from {
        top: 2px;
        left: 2px;
        rigth: 2px;
        bottom: 2px; }
    to {
        top: 50%;
        left: 50%;
        right: 50%;
        bottom: 50%; } }


@keyframes check-out {
    from {
        top: 50%;
        left: 50%;
        right: 50%;
        bottom: 50%; }
    to {
        top: 2px;
        left: 2px;
        rigth: 2px;
        bottom: 2px; } }
